import React from "react";
import PropTypes from "prop-types";

import st from "./style";
import ImageColorFilter from '../../cards/ImageColorFilter';
import IconPlaceHolderCover from '../../icons/IconPlaceHolderCover';

class ProductPriceCard2 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      
    };
  }

  render() {

    const formatPrice = price => {
      return `R$${price.replace('.',',')}`;
    }

    return (
      <div style={{...st.container,...this.props.style}}>

        <div style={st.ctnImage}>
        {
          this.props.img?
          <div>
            <ImageColorFilter src={this.props.img} style={st.img} fromColor='rgba(255, 255, 255, 0) 0%' toColor='rgba(0, 0, 0, 0) 100%' />
          </div>
          :
          <div style={st.ctnPlaceholder}>
            <IconPlaceHolderCover style={st.iconPlaceHolder}/>
          </div> 
        }

        </div>
        <div style={st.ctnContent}>
          <p style={st.pAuthor}>{this.props.author}</p>
          <p style={st.pTitle}>{this.props.productTitle}</p>
          <div style={st.ctnAmount}>
            <div>
              <span style={st.pPrice}>{formatPrice(this.props.price)}</span>
              <span style={st.pUnit}> / {this.props.unit}</span>
            </div>
          { this.props.serves?
            <div>
              <span style={st.pCountLabel}> serve</span>
              <span style={st.pCount}> {this.props.serves}</span>
            </div>
            :null
          }
          </div>
          
        </div> 
        {
          this.props.deliveryDays?
          <div style={st.ctnDelivery}>
            <span style={st.deliveryL}>Em até</span>
            <span style={st.deliveryR}> {this.props.deliveryDays} {parseInt(this.props.deliveryDays)>1?'dias':'dia'}</span>
          </div>
          :null
        }

      </div>
    );
  }
}

ProductPriceCard2.propTypes = {
  onClick: PropTypes.func,
  productTitle: PropTypes.string,
  author: PropTypes.string,
  price: PropTypes.string,
  unit: PropTypes.string,
  serves: PropTypes.string,
  deliveryDays: PropTypes.string,
  img: PropTypes.string,
  style: PropTypes.object,
};
ProductPriceCard2.defaultProps = {
  onClick: () => {},
  style: {},
};

export default ProductPriceCard2;